import React, { Component } from 'react';
import { Row, Col, Form } from 'antd';
import moment from 'moment';
import { BtnSearch, BtnClear } from 'components/common/button';
import SelectAjax from 'components/common/input/SelectAjax';
import { getEducationPlans } from 'services/educationPlan';

@Form.create({ name: 'FormSearch' })
class FormSearch extends Component {
  state = {};

  handleReset = () => {
    this.props.form.resetFields();
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      this.props.handleSubmit(values);
    });
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form layout="vertical" className="form-inline-label" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <Col span={8}>
            <Form.Item label="Năm học">
              {getFieldDecorator('yearEduId', {})(
                <SelectAjax
                  form={this.props.form}
                  allowGetObjSelected
                  service={getEducationPlans}
                  typeValue="int"
                  setLabel={yearEdu =>
                    `${moment(yearEdu.fromDate).year()}-${moment(yearEdu.toDate).year()}`
                  }
                />
              )}
            </Form.Item>
          </Col>
          <Col span={2}>
            <div className="btn-group">
              <BtnSearch htmlType="submit" />
              <BtnClear onClick={this.handleReset} />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormSearch;



