import { request } from 'utils';

const prefix = '/api/education';

export const getGetHolidayByYear = ({
  yearEduId,
  page,
  pageSize = 1000,
  orderBy,
  isPagingEnabled = false,
} = {}) =>
  request({
    prefix,
    url: '/Holiday/GetHolidayByYear',
    method: 'GET',
    params: { yearEduId, page, pageSize, orderBy, isPagingEnabled },
    data: {},
  });

export const createUpdateHoliday = ({ holidays } = {}) =>
  request({
    prefix,
    url: '/Holiday/CreateUpdateHoliday',
    method: 'POST',
    params: {},
    data: { holidays },
  });

export const deleteHolidays = ({ ids } = {}) =>
  request({
    prefix,
    url: `/Holiday/DeleteHolidays`,
    method: 'DELETE',
    params: {},
    data: { ids },
  });



